@import '../../styles/index.scss';

.part2_production{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 50px;
  padding-bottom: 50px;
}

.flex{
  display: flex;
}

.part2_production_paragraphe1{
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: $primary-font-size;
  padding: 70px;
  border-radius: 25px;
  background-color: $secondary-color;
  margin: 15px;
  color: white;
}

.part2_production_paragraphe2{
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: $primary-font-size;
  padding: 70px;
  border-radius: 25px;
  background-color: $primary-color;
  margin: 15px;
}


@keyframes slide-inText {
  0% {
    transform: translateX(0%);
    opacity: 0;
  }
  100% {
    transform: translateX(0%);
    opacity: 1;
  }
}

/* --- tonduFondu ---*/

.tonduFondu{
  width: 100%;
  margin-bottom: 50px;
}

.titleTonduFondu{
  margin-top: 50px;
  margin-left: 50px;
  font-size: 80px;
  color: $primary-color;
  font-family: $secondary-font;
}

.image-container {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: url('../../assets/cursorTondeuse.cur'), auto;
}

.fade-out {
  opacity: 0;
}

.fade-in {
  opacity: 1;
}

.imagetonduFondu{
  width: 200px;
}

/* --- productionImages ---*/

.productionImages{
  display: flex;
  justify-content: space-around;
  margin: 100px;
  margin-left: 100px;
  margin-right: 100px;
}

@media only screen and (max-width: 767px) {
  .part2_production{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: 50px;
    padding-bottom: 50px;
    padding-left: 15px;
    padding-right: 15px;
  }
  
  
  .part2_production{
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      padding: 15px;
      padding-bottom: 50px;
  }

  .flex{
      display: flex;
      flex-direction: column;
  }

  .part2_production_paragraphe1 {
      font-size: calc($primary-font-size * 0.8); /* Réduction de la taille de la police */
      padding: 30px; /* Réduction du padding */
      margin: 10px; /* Réduction des marges */
  }

  .part2_production_paragraphe2 {
      font-size: calc($primary-font-size * 0.8); /* Réduction de la taille de la police */
      padding: 30px; /* Réduction du padding */
      text-align: center; /* Centre le texte pour mobile */
      margin: 10px; /* Réduction des marges */
  }


  .titleTonduFondu{
    margin-top: 50px;
    margin-left: 15px;
    font-size: 42px;
    margin-bottom: 50px;
  }

  .productionImages{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    margin: 0px;
    margin-left: 0px;
    margin-right: 0px;
    margin-bottom: 50px;
    padding-left: 15px;
    padding-right: 15px;
  }
}